import React, { Fragment, Component } from 'react';
import logo from '../../assets/icons/logo-2.png';
import avatar from '../../assets/icons/avatar.png';
import { useDispatch, connect } from 'react-redux';
import { Link, Redirect } from 'react-router-dom';
import { signOut } from '../../services/redux/action';

class Header extends Component {

	handleSignOut = async () => {
		const res = await this.props.signOutFirebase().catch(err => err);
		if (res === true) {
			return (
				<Redirect to="/login" />
			)
		}
	}

	render() {
		return (
			<Fragment>
				<div className="navbar-bg"></div>
				<nav className="navbar navbar-expand-lg main-navbar">
					<Link to="/dashboard" className="navbar-brand sidebar-gone-hide">
						<img src={logo} width="150" alt="logo" />
					</Link>
					<div className="navbar-nav">
						<a className="nav-link sidebar-gone-show" data-toggle="sidebar"><i className="fas fa-bars"></i></a>
					</div>
					<div className="ml-auto"></div>
					<ul className="navbar-nav navbar-right">
						<li className="dropdown"><a data-toggle="dropdown"
							className="nav-link dropdown-toggle nav-link-lg nav-link-user">
							<img alt="image" src={avatar} className="mr-1" style={{ transform: 'scale(1.2)' }} />
							<div className="d-sm-none d-lg-inline-block">Hi, Admin</div>
						</a>
							<div className="dropdown-menu dropdown-menu-right">
								<div className="dropdown-divider"></div>
								<a className="dropdown-item has-icon text-danger" onClick={this.handleSignOut}>
									<i className="fas fa-sign-out-alt"></i> Logout
								</a>
							</div>
						</li>
					</ul>
				</nav>
			</Fragment>
		);
	}
}

const reduxDispatch = (dispatch) => ({
	signOutFirebase: () => dispatch(signOut()),
});

export default connect(null, reduxDispatch)(Header);