const userDataLocalStorage = () => {
  try {
    const userDataLocalStorage = localStorage.getItem('userData');
    const userData = JSON.parse(userDataLocalStorage);
    return userData;
  } catch (error) {
    return {};
  }
}

const initialState = {
  isLogin: false,
  isLoading: false,
  user: userDataLocalStorage(),
}

const reducer = (state = initialState, action) => {
  if (action.type === 'CHANGE_IS_LOGIN') {
    return {
      ...state,
      isLogin: action.value,
    }
  }

  if (action.type === 'CHANGE_IS_LOADING') {
    return {
      ...state,
      isLoading: action.value,
    }
  }

  if (action.type === 'CHANGE_USER') {
    return {
      ...state,
      user: action.value,
    }
  }

  return state;
}

export default reducer;