import React, { Component, Fragment } from 'react';
import MUIDataTable from 'mui-datatables';
import firebase from '../../../../services/firebase';

class UserListing extends Component {

    constructor(props) {
        super(props);
        this.ref = firebase.firestore().collection('users');
        this.unsubscribe = null;
        this.state = {
            users: [],
            isLoading: false,
        };
    }

    onCollectionUpdate = async (querySnapshot) => {
        const users = [];
        await querySnapshot.forEach((doc) => {
            const { fullName, dateOfBirth, email } = doc.data();
            users.push({
                key: doc.id,
                fullName,
                dateOfBirth: dateOfBirth.toDate().toLocaleString("en-US", { dateStyle: "long" }),
                email,
                age: this.getAge(dateOfBirth.toMillis()),
            });
        });
        // console.log(querySnapshot);
        this.setState({
            users,
        });
    }

    getAge = (dob) => {
        let diff = new Date() - dob;
        let ageDate = new Date(diff);
        return Math.abs(ageDate.getUTCFullYear() - 1970);
    }

    componentWillMount() {
        this.unsubscribe = this.ref.onSnapshot(this.onCollectionUpdate);
        firebase.firestore().collection("users").add({

            fullName: 'John',
            dateOfBirth: new Date("1998-01-28"),
            email: 'yasa13darma@gmail.com',
        }).then(doc => {
            console.log(doc);
            // this.props.history.push('/content');
        }).catch(error => {
            // this.setState({
            //   formSubmited: false,
            // });
            console.log(error);
        });
    }

    // componentWillUpdate() {
    //     return true;
    // }


    render() {
        const columns = ["#", "Name", "Email", "Age", "Date of Birth"];
        const users = this.state.users;
        const data = [];
        users.map((user, index) => {
            let userData = [++index, user.fullName, user.email, user.age, user.dateOfBirth];
            data.push(userData);
        });
        // const data = Object.values(users);
        console.log(data);
        // console.log(this.state.users);
        return (
            <Fragment>
                <div className="section-header">
                    <h1>Users</h1>
                </div>
                <div className="section-body">
                    <div className="row">
                        <div className="col-12">
                            <div className="card">
                                <div className="card-header">
                                    <h4>List Users</h4>
                                </div>
                                <div className="card-body">
                                    <MUIDataTable
                                        title={"Users List"}
                                        data={data}
                                        columns={columns}
                                        options={{
                                            elevation: 0,
                                            selectableRows: false // <===== will turn off checkboxes in rows
                                        }}
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </Fragment>
        );
    }
}

export default UserListing;