import React, { Component, Fragment } from 'react';
import { Doughnut } from 'react-chartjs-2';
import firebase from '../../../services/firebase';

class DashboardHome extends Component {
  constructor(props) {
    super(props);
    this.ref = firebase.firestore().collection('users');
    this.chartReference = React.createRef();
    this.unsubscribe = null;
    this.state = {
      labels: ['Kids', 'Tenagers', 'Adults'],
      datasets: [
        {
          label: 'Age Users',
          backgroundColor: [
            '#B21F00',
            '#C9DE00',
            '#2FDE00',
          ],
          hoverBackgroundColor: [
            '#501800',
            '#4B5000',
            '#175000',
          ],
          data: [65, 59, 80]
        }
      ]
    };
  }

  onCollectionUpdate = (querySnapshot) => {
    let data = [0, 0, 0];
    querySnapshot.forEach((doc) => {
      const { fullName, dateOfBirth, email } = doc.data();
      let age = this.getAge(dateOfBirth.toMillis());
      if (age > 20) {
        data[2]++;
      } else if (age > 12) {
        data[1]++;
      } else {
        data[0]++;
      }
    });
    console.log(data);
    this.setState(prevState => ({
      datasets: prevState.datasets.map(
        obj => Object.assign(obj, { data: data })
      )
    }));
  }

  getAge = (dob) => {
    let diff = new Date() - dob;
    let ageDate = new Date(diff);
    return Math.abs(ageDate.getUTCFullYear() - 1970);
  }

  componentWillMount() {
    this.unsubscribe = this.ref.onSnapshot(this.onCollectionUpdate);
    // firebase.firestore().collection("users").add({

    //     fullName: 'John',
    //     dateOfBirth: new Date("1998-01-28"),
    //     email: 'yasa13darma@gmail.com',
    // }).then(doc => {
    //     console.log(doc);
    //     // this.props.history.push('/content');
    // }).catch(error => {
    //     // this.setState({
    //     //   formSubmited: false,
    //     // });
    //     console.log(error);
    // });
  }


  render() {
    return (
      <Fragment>
        <div className="section-header">
          <h1>Dashboard</h1>
        </div>
        <div className="section-body">
          <div className="row">
            <div className="col-12">
              <div className="card">
                <div className="card-header">
                  <h4>Dashboard</h4>
                </div>
                <div className="card-body">
                  <Doughnut data={this.state} ref={this.chartReference} />
                </div>
                <div className="card-footer text-right">
                </div>
              </div>
            </div>
          </div>
        </div>
      </Fragment>
    );
  }
}

export default DashboardHome;