import React, { Component, Fragment } from 'react';
import firebase from '../../../../services/firebase';
import FileUploader from 'react-firebase-file-uploader';

class ContentUpdate extends Component {
  state = {
    uid: '',
    label: '',
    latinName: '',
    englishName: '',
    englishDescription: '',
    englishAudio: '',
    indonesianName: '',
    indonesianDescription: '',
    indonesianAudio: '',
    isLoading: { id: false, en: false },
    progress: { id: 0, en: 0 },
    formSubmited: false,
  };

  getData = async () => {
    const { id } = this.props.match.params;
    // console.log(id);
    await firebase.firestore().collection('contents').doc(id).get()
      .then((doc) => {
        const { label, latinName, indonesian, english } = doc.data();
        this.setState({
          uid: id,
          label: label,
          latinName: latinName,
          indonesianName: indonesian.name,
          indonesianDescription: indonesian.description,
          indonesianAudio: indonesian.audio,
          englishName: english.name,
          englishDescription: english.description,
          englishAudio: english.audio,
        });
      })
      .catch((error) => {
        console.log('Error getting data')
      });
  }


  handleFormChange = (e) => {
    this.setState({
      [e.target.name]: e.target.value,
    });
    // console.log(this.state);
  }

  handleUploadStart = lang => () => {
    switch (lang) {
      case 'id':
        this.setState(({ isLoading, progress }) => ({
          isLoading: {
            ...isLoading,
            id: true,
          },
          progress: {
            ...progress,
            id: 0,
          }
        }));
        break;
      case 'en':
        this.setState(({ isLoading, progress }) => ({
          isLoading: {
            ...isLoading,
            en: true,
          },
          progress: {
            ...progress,
            en: 0,
          }
        }));
        break;
      default:
        break;
    }
  };
  handleProgress = lang => progress => {
    switch (lang) {
      case 'id':
        this.setState(({ progress }) => ({
          progress: {
            ...progress,
            id: progress.id,
          }
        }));
        break;
      case 'en':
        this.setState(({ progress }) => ({
          progress: {
            ...progress,
            en: progress.en,
          }
        }));
        break;
      default:
        break;
    }
  }
  handleUploadError = lang => error => {
    switch (lang) {
      case 'id':
        this.setState(({ isLoading }) => ({
          isLoading: {
            ...isLoading,
            id: false,
          }
        }));
        break;
      case 'en':
        this.setState(({ isLoading }) => ({
          isLoading: {
            ...isLoading,
            en: false,
          }
        }));
        break;
      default:
        break;
    }
    console.error(lang, error);
  };
  handleUploadSuccess = lang => filename => {
    switch (lang) {
      case 'id':
        this.setState(({ isLoading, progress }) => ({
          isLoading: {
            ...isLoading,
            id: false,
          },
          progress: {
            ...progress,
            id: 100,
          }
        }));
        firebase
          .storage()
          .ref("audios")
          .child(filename)
          .getDownloadURL()
          .then(url => this.setState({ indonesianAudio: url }));
        break;
      case 'en':
        this.setState(({ isLoading, progress }) => ({
          isLoading: {
            ...isLoading,
            en: false,
          },
          progress: {
            ...progress,
            en: 100,
          }
        }));
        firebase
          .storage()
          .ref("audios")
          .child(filename)
          .getDownloadURL()
          .then(url => this.setState({ englishAudio: url }));
        break;
      default:
        break;
    }
  };

  componentWillMount() {
    this.getData();
  }

  handleFormSubmit = async () => {
    console.log(this.state);
    this.setState({
      formSubmited: true,
    });
    const { uid, label, latinName, englishName, englishDescription, englishAudio, indonesianName, indonesianAudio, indonesianDescription } = this.state;

    await firebase.firestore().collection("contents").doc(uid).update({
      label,
      latinName,
      indonesian: {
        name: indonesianName,
        description: indonesianDescription,
        audio: indonesianAudio,
      },
      english: {
        name: englishName,
        description: englishDescription,
        audio: englishAudio,
      },
    }).then(doc => {
      console.log(doc);
      this.props.history.push('/content');
    }).catch(error => {
      this.setState({
        formSubmited: false,
      });
      console.log(error);
    });
  }

  componentDidMount() {
    console.log(this.props.match.params);
  }

  render() {
    console.log(this.state);
    const { label, latinName, indonesianName, indonesianDescription, indonesianAudio, englishName, englishDescription, englishAudio } = this.state;
    return (
      <Fragment>
        <div className="section-header">
          <h1>Manage Contents</h1>
        </div>
        <div className="section-body">
          <div className="row">
            <div className="col-12">
              <div className="card">
                <div className="card-header">
                  <h4>Update Data</h4>
                </div>
                <div className="card-body">
                  <div className="form-group">
                    <label>Class/Label</label>
                    <input type="text" name="label" className="form-control" value={label} disabled />
                    {/* <select name="label" id="" className="custom-select" onChange={this.handleFormChange} required>
                      <option value="" selected disabled>-Choose One-</option>
                      <option value="komodo_dragon"  >Komodo Dragon</option>
                      <option value="bird_of_paradise">Bird Of Paradise</option>
                      <option value="maleo">Maleo</option>
                      <option value="bali_myna">Bali Myna</option>
                      <option value="proboscis_monkey">Proboscis Monkey</option>
                    </select> */}
                  </div>
                  <div className="form-group">
                    <label htmlFor="latinName">Latin Name</label>
                    <input type="text" name="latinName" className="form-control" value={latinName} onChange={this.handleFormChange} />
                  </div>
                  <div className="form-group">
                    <div className="d-flex justify-content-between align-items-center">
                      <label>Name</label>
                      <ul className="nav nav-pills mb-3" id="name-tab" role="tablist">
                        <li className="nav-item">
                          <a className="nav-link active" id="name-en-tab" data-toggle="pill" href="#name-en" role="tab" aria-controls="name-en" aria-selected="true">English</a>
                        </li>
                        <li className="nav-item">
                          <a className="nav-link" id="name-in-tab" data-toggle="pill" href="#name-in" role="tab" aria-controls="name-in" aria-selected="false">Indonesia</a>
                        </li>
                      </ul>
                    </div>
                    <div className="tab-content" id="name-tabContent">
                      <div className="tab-pane fade show active" id="name-en" role="tabpanel" aria-labelledby="name-en-tab">
                        <input type="text" name="englishName" value={englishName} className="form-control" onChange={this.handleFormChange} />
                      </div>
                      <div className="tab-pane fade" id="name-in" role="tabpanel" aria-labelledby="name-in-tab">
                        <input type="text" name="indonesianName" value={indonesianName} className="form-control" onChange={this.handleFormChange} />
                      </div>
                    </div>
                  </div>
                  <div className="form-group">
                    <div className="d-flex justify-content-between align-items-center">
                      <label>Description</label>
                      <ul className="nav nav-pills mb-3" id="desc-tab" role="tablist">
                        <li className="nav-item">
                          <a className="nav-link active" id="desc-en-tab" data-toggle="pill" href="#desc-en" role="tab" aria-controls="desc-en" aria-selected="true">English</a>
                        </li>
                        <li className="nav-item">
                          <a className="nav-link" id="desc-in-tab" data-toggle="pill" href="#desc-in" role="tab" aria-controls="desc-in" aria-selected="false">Indonesia</a>
                        </li>
                      </ul>
                    </div>
                    <div className="tab-content" id="desc-tabContent">
                      <div className="tab-pane fade show active" id="desc-en" role="tabpanel" aria-labelledby="desc-en-tab">
                        <textarea name="englishDescription" className="form-control" onChange={this.handleFormChange} style={{ minHeight: '150px' }} value={englishDescription}></textarea>
                      </div>
                      <div className="tab-pane fade" id="desc-in" role="tabpanel" aria-labelledby="desc-in-tab">
                        <textarea name="indonesianDescription" className="form-control" onChange={this.handleFormChange} style={{ minHeight: '150px' }} value={indonesianDescription}></textarea>
                      </div>
                    </div>
                  </div>
                  <div className="form-group">
                    <label>Audio</label>
                    <div className="input-group mb-3">
                      <div className="input-group-prepend mr-2">
                        <span className="input-group-text" id="basic-addon3">English</span>
                      </div>
                      <FileUploader
                        // hidden
                        className="flex-grow-1"
                        accept="audio/*"
                        name="englishAudio"
                        randomizeFilename
                        storageRef={firebase.storage().ref('audios')}
                        onUploadStart={this.handleUploadStart("en")}
                        onUploadError={this.handleUploadError("en")}
                        onUploadSuccess={this.handleUploadSuccess("en")}
                        onProgress={this.handleProgress("en")}
                      />
                      <div className="input-group-append">
                        <audio src={englishAudio} controls></audio>
                      </div>
                    </div>
                    {this.state.progress.en === 100 ? <span>Upload Done!</span> : ''}
                    <div className="input-group mb-3">
                      <div className="input-group-prepend mr-2">
                        <span className="input-group-text" id="basic-addon3">Indonesia</span>
                      </div>
                      <FileUploader
                        // hidden
                        className="flex-grow-1"
                        accept="audio/*"
                        name="indonesianAudio"
                        randomizeFilename
                        storageRef={firebase.storage().ref('audios')}
                        onUploadStart={this.handleUploadStart("id")}
                        onUploadError={this.handleUploadError("id")}
                        onUploadSuccess={this.handleUploadSuccess("id")}
                        onProgress={this.handleProgress("id")}
                      />
                      <div className="input-group-append">
                        <audio src={indonesianAudio} controls></audio>
                      </div>
                    </div>
                    {this.state.progress.id === 100 ? <span>Upload Done!</span> : ''}
                  </div>
                </div>
                <div className="card-footer text-right">
                  <button className={`btn btn-primary ${this.state.formSubmited ? 'btn-progress btn-disabled' : ''}`} type="submit" onClick={this.handleFormSubmit}>Submit</button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </Fragment>
    );
  }
}

export default ContentUpdate;