import React, { Suspense, lazy, Fragment, Component } from 'react';
import firebase from './services/firebase';
import { createStore } from 'redux';
import { Provider } from 'react-redux';
import { BrowserRouter as Router, withRouter, Switch, Route, Redirect, Link } from 'react-router-dom';

//services
import { store } from './services/redux';

//component
import Spinner from './components/Spinner';
import AuthLayoutRoute from './pages/AuthLayout';
import DashboardLayoutRoute from './pages/DashboardLayout';
import DashboardHome from './pages/DashboarLayout/DashboardHome';
import ContentUpdate from './pages/DashboarLayout/Contents/Update';
import UserListing from './pages/DashboarLayout/Users/Listing';

//pages
const ClassForm = React.lazy(() => import('./pages/DashboarLayout/Classes/Form'));
const ClassListing = React.lazy(() => import('./pages/DashboarLayout/Classes/Listing'));
const ContentListing = React.lazy(() => import('./pages/DashboarLayout/Contents/Listing'));
const ContentCreate = React.lazy(() => import('./pages/DashboarLayout/Contents/Create'));
const Login = React.lazy(() => import('./pages/FullLayout/Login'));
const logo = React.lazy(() => import('./logo.svg'));
// import './App.css';


const NotMatch = () => {
  return (
    <div className="d-flex justify-content-center align-items-center text-center" style={{ height: '100vh' }}>
      <div>
        <h5>404 Not Found</h5>
        <Link to="/dashboard">Go Back</Link>
      </div>
    </div>
  )
}

class App extends Component {
  render() {
    return (
      <Provider store={store}>
        <Router>
          <Suspense fallback={<Spinner />}>
            <Switch>
              <Route exact path="/">
                <Redirect to="/dashboard" />
              </Route>
              <AuthLayoutRoute exact path="/login" component={Login} />
              <DashboardLayoutRoute exact path="/dashboard" component={DashboardHome} />
              <DashboardLayoutRoute exact path="/content" component={ContentListing} />
              <DashboardLayoutRoute exact path="/users" component={UserListing} />
              <DashboardLayoutRoute exact path="/content/create" component={ContentCreate} />
              <DashboardLayoutRoute exact path="/content/update/:id" component={ContentUpdate} />

              <Route component={NotMatch} />
            </Switch>
          </Suspense>
        </Router>
      </Provider>
    );
  }


}

export default (App);


