import React, { Component } from 'react';
import { Link, withRouter } from 'react-router-dom';

class Navbar extends Component {

  render() {
    return (
      <nav className="navbar navbar-secondary navbar-expand-lg" >
        <div className="container">
          <nav className="navbar-nav">
            <li className={`nav-item ${this.props.location.pathname.includes('/dashboard') ? 'active' : ''}`}>
              <Link to="/dashboard" className="nav-link"><i className="fas fa-fire"></i><span>Dashboard</span></Link>
            </li>
            <li className={`nav-item ${this.props.location.pathname.includes('/content') ? 'active' : ''}`}>
              <Link to="/content" className="nav-link"><i className="fas fa-file"></i><span>Contents</span></Link>
            </li>
            <li className={`nav-item ${this.props.location.pathname.includes('/users') ? 'active' : ''}`}>
              <Link to="/users" className="nav-link"><i className="fas fa-users"></i><span>Users</span></Link>
            </li>
          </nav>
        </div>
      </nav>
    );
  }
}

export default (withRouter(Navbar));