import firebase from '../../firebase';

export const actionChangeUser = () => (dispatch) => {
  return dispatch({ type: 'CHANGE_USER', value: { name: 'Ganti' } })
}

export const loginFirebase = (data) => (dispatch) => {
  const { email, password } = data;
  // dispatch({type: 'CHANGE_LOADING'})
  dispatch({ type: 'CHANGE_IS_LOADING', value: true })
  return new Promise((resolve, reject) => {
    firebase.auth().signInWithEmailAndPassword(email, password)
      .then(res => {
        console.log(res);
        const { email, emailVerified, uid } = res.user;
        localStorage.setItem('userData', JSON.stringify({ uid, email, emailVerified }));
        dispatch({
          type: 'CHANGE_USER', value: {
            email, emailVerified, uid
          }
        })
        dispatch({ type: 'CHANGE_IS_LOGIN', value: true })
        resolve(true);
      })
      .catch((error) => {
        dispatch({ type: 'CHANGE_IS_LOGIN', value: false });
        reject(false);
      })
      .finally(() => {
        dispatch({ type: 'CHANGE_IS_LOADING', value: false });
      })
  })
}

export const signOut = () => (dispatch) => {
  return new Promise((resolve, reject) => {
    firebase.auth().signOut().then(() => {
      localStorage.removeItem('userData');
      dispatch({
        type: 'CHANGE_USER',
        value: null,
      })
      dispatch({ type: 'CHANGE_IS_LOGIN', value: false })
      resolve(true);
    }).catch(error => {
      reject(false);
      // An error happened.
    });
  })
}